* {
  margin: 0px;
  padding: 0px;
}
#container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 30px;
  gap: 9px;

  overflow: auto;

  min-width: max-content;
  /* min-width: min-content; */
}
div table {
  background-color: black;
  color: white;
  padding: 10px;
  font-size: 50px;
  border-spacing: 30px;
}

input,
button {
  background-color: black;
  color: white;
  outline: none;
  border: 1px solid black;
  font-size: 30px;
}
td {
  background-color: red;
  /* width: 100px; */
  height: 115px;
  border: 12px solid white;
  text-align: center;
  border-radius: 50%;
  font-weight: bold;
  padding: 12px;

  align-items: center;
  text-align: center;
  justify-content: center;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}

.round p {
  width: 130px;
  font-size: 30px;
}

/* Fetch Data Styles */
.fetch-data {
  background-color: black;
  color: white;
}

#posts-data {
  padding: 50px;
}

@media only screen and (max-width: 600px) {
  .fetch-data {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 950px;
    background-color: black;
    color: white;
  }
}
